<template>
  <div>
    <v-flex>
      <v-toolbar color="grey darken-3" class="white--text">
        <v-toolbar-title>{{ stock.name }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <p>£{{ stock.price }}</p>
      </v-toolbar>
      <v-card>
        <v-container>
          <v-text-field
            label="Please select quantity"
            placeholder="Quantity"
            box
            type="number"
            min="0"
            v-model.number="quantity"
          ></v-text-field>
          <v-btn
            class="white--text"
            color="red darken-3"
            @click="buyStock"
            :disabled="insufficientFunds || quantity <= 0 || !Number.isInteger(quantity)"
          >{{ insufficientFunds ? 'Insufficient Funds' : 'Buy' }}</v-btn>
          <v-spacer></v-spacer>
          <v-alert v-model="alert" dismissible color="teal" icon="check_circle" outline>Success</v-alert>
        </v-container>
      </v-card>
    </v-flex>
  </div>
</template>
<script>
// import { mapState } from 'vuex'
export default {
  props: ["stock"],
  data() {
    return {
      quantity: 0,
      alert: false
      // for table
    };
  },
  computed: {
    funds() {
      return this.$store.getters.funds;
    },
    insufficientFunds() {
      return this.quantity * this.stock.price > this.funds;
    }
  },
  methods: {
    buyStock() {
      const order = {
        stockId: this.stock.id,
        stockPrice: this.stock.price,
        quantity: this.quantity
      };
      this.$store.dispatch("buyStock", order);
      this.quantity = 0;
      this.alert = true;
    }
  }
};
</script>
<style scoped>
.v-btn {
  margin: 6px 0;
}
</style>
