<template>
  <div>
    <v-flex>
      <v-toolbar color="grey darken-3" class="white--text">
        <v-toolbar-title>{{ stock.name }}</v-toolbar-title>
        <v-subheader class="white--text">Quantity: {{ stock.quantity }}</v-subheader>
        <v-subheader class="white--text">Price: {{ stock.price }}</v-subheader>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-card>
        <v-container>
          <v-text-field
            label="Please select quantity"
            placeholder="Quantity"
            box
            type="number"
            v-model.number="quantity"
          ></v-text-field>
          <v-btn
            class="white--text"
            color="red darken-3"
            @click="sellStock"
            :disabled="insufficientQuantity || quantity <= 0 || !Number.isInteger(quantity)"
          >{{ insufficientQuantity ? 'Not enough' : 'Sell' }}</v-btn>
        </v-container>
      </v-card>
    </v-flex>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  props: ['stock'],
  data () {
    return {
      quantity: 0
    }
  },
  computed: {
    insufficientQuantity () {
      return this.quantity > this.stock.quantity
    }
  },
  methods: {
    ...mapActions({
      placeSellOrder: 'sellStock'
    }),
    sellStock () {
      const order = {
        stockId: this.stock.id,
        stockPrice: this.stock.price,
        quantity: this.quantity
      }
      this.placeSellOrder(order)
      this.quantity = 0
    }
  }
}
</script>
