<template>
  <div>
    <v-navigation-drawer v-model="drawer" fixed right app>
      <v-list dense>
        <!-- portfolio link -->
        <v-list-tile to="/portfolio">
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Our Domains</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <!-- stocks link -->
        <v-list-tile to="/stocks">
          <v-list-tile-action>
            <v-icon>attach_money</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Buy Domains</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click="saveData">
          <v-list-tile-action>
            <v-icon>save</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Save to Database</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click="loadData">
          <v-list-tile-action>
            <v-icon>autorenew</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Load from Database</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile>
          <v-list-tile-action>
            <v-icon>account_balance</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Funds: {{ funds | currency }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click="endDay">
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>End Day (randomise prices)</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar color="grey darken-1" dark fixed app>
      <v-spacer></v-spacer>
      <v-toolbar-title>
        <router-link to="/" tag="h3">Dash2</router-link>
      </v-toolbar-title>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
    </v-toolbar>
  </div>
</template>
<script>
import { mapActions } from 'vuex'

export default {
  data: () => ({
    drawer: null
  }),
  props: {
    source: String
  },
  computed: {
    funds () {
      return this.$store.getters.funds
    }
  },
  methods: {
    ...mapActions({
      randomizeStocks: 'randomizeStocks',
      fetchData: 'loadData'
    }),
    endDay () {
      this.randomizeStocks()
    },
    saveData () {
      const data = {
        funds: this.$store.getters.funds,
        stockPortfolio: this.$store.getters.stockPortfolio,
        stocks: this.$store.getters.stocks
      }
      this.$http.put('data.json', data)
    },
    loadData () {
      this.fetchData()
    }
  }
}
</script>
<style>
h3 {
  cursor: pointer;
}
</style>
